import { faChevronDown } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Accordion, AccordionDetails, AccordionSummary } from "@mui/material";
import React from "react";

const Profile = () => {
  const path = process.env.PUBLIC_URL;
  return (
    <section className="profile scroll">
      <div className="inner">
        <h2 data-aos="fade-left">About</h2>
        <div className="about">
          <div className="about-left">
            <img src={`${path}/images/profile.jpg`} alt="pic" />
          </div>
          <div className="about-right">
            <div className="about-title">
              <div className="about-top">
                <span>
                  ,  <span className="color"></span>
                </span>
                <img
                  src={`${path}/images/logo.png`}
                  alt="logo"
                  className="about-logo"
                />
              </div>
              <p>
                <span className="color"></span> &nbsp;
                <span className="color"></span>
                &nbsp;.
              </p>
            </div>
            <p className="about-detail">
                   &nbsp;
              <b>   </b>
              . ,&nbsp;
              <b>      </b>  
                     .
              &nbsp;
              <b></b>&nbsp;
              <b></b>   ,    
                  &nbsp;
              <b> </b>  .
            </p>
          </div>
        </div>
        <div className="skills">
          {/* <h3>Skills</h3> */}
          <Accordion className="accordion">
            <AccordionSummary
              className="accordion-title"
              expandIcon={<FontAwesomeIcon icon={faChevronDown} />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <div className="skills-title">Language</div>
              <div className="skills-list">
                <span>JavaScript</span>
                <span>TypeScript</span>
              </div>
            </AccordionSummary>
            <AccordionDetails className="accordion-detail">
              <p>
                 JavaScript ES6   JS    
                 ,     
                TypeScript  .
              </p>
            </AccordionDetails>
          </Accordion>
          <Accordion className="accordion">
            <AccordionSummary
              className="accordion-title"
              expandIcon={<FontAwesomeIcon icon={faChevronDown} />}
              aria-controls="panel2a-content"
              id="panel2a-header"
            >
              <div className="skills-title">Framework / Library</div>
              <div className="skills-list">
                <span>React</span>
                <span>Redux-toolkit</span>
                <span>Emotion</span>
                <span>Tailwind</span>
              </div>
            </AccordionSummary>
            <AccordionDetails className="accordion-detail">
              <p>
                 React 18     , 
                   Redux   .
              </p>
              <p>
                 Emotion, Tailwind      
                 .
              </p>
            </AccordionDetails>
          </Accordion>
          <Accordion className="accordion">
            <AccordionSummary
              className="accordion-title"
              expandIcon={<FontAwesomeIcon icon={faChevronDown} />}
              aria-controls="panel2a-content"
              id="panel2a-header"
            >
              <div className="skills-title">Others</div>
              <div className="skills-list">
                <span>git</span>
                <span>github</span>
                <span>Slack</span>
                <span>Notion</span>
              </div>
            </AccordionSummary>
            <AccordionDetails className="accordion-detail">
              <p>
                 git   ,     . github
                     .
              </p>
              <p> slack  notion    .</p>
            </AccordionDetails>
          </Accordion>
        </div>
      </div>
    </section>
  );
};

export default Profile;
